import React from "react";
import { signIn, signOut, useSession } from "next-auth/react";

const Auth = () => {
  const { data: session } = useSession();

  if (session) {
    return (
      <>
        {session.user?.name}

        <button
          onClick={() => {
            signOut();
          }}
        >
          Sign out
        </button>
      </>
    );
  }

  return (
    <>
      Not Signed in
      <button onClick={() => signIn()}>Sign In</button>
    </>
  );
};

const NavMenu = () => {
  return (
    <div>
      <Auth />
    </div>
  );
};

export default NavMenu;
